<template>
	<view class="box">
		<view class="info-li">
			<view class="left">
				<view class="title">
					费用
				</view>
			</view>
			<view class="info-li-input">{{money | formatNum}}</view>
		</view>
		<view class="info-li">
			<view class="left">
				<view class="title">
					合计
				</view>
			</view>
			<view class="info-li-input">{{money | formatNum}}</view>
		</view>
		<view class="next-btn" @tap="save">
			下一步
		</view>
	</view>
</template>

<script>
	let seqId = "";
	let type = 1;
	import {mapState} from 'vuex';
	export default {
		data() {
			return {
				money: ""
			}
		},
		onLoad(option) {
			seqId = option.seqId
			type = option.type
			this.loadMoney()
		},
		computed:{
			...mapState({
				type_check:state=>state.type_check,
			})
		},
		methods: {
			loadMoney() {
				this.$api.get('/sysDict/list/MERCHANTRZJE001', {
					params: {}
				}).then(res => {
					if (res.code == 200) {
						this.money = res.data.value
					}
				})
			},
			save() {
				// #ifdef MP-WEIXIN
				let that = this;
				that.$api.post('/payInfo/saveMerchantPay', {
					"seqId": seqId,
					"type": type
				}).then(res => {
					if (res.code == 200) {
						let paymentData = res.data;
						uni.requestPayment({
							timeStamp: paymentData.timeStamp,
							nonceStr: paymentData.nonceStr,
							package: paymentData.package,
							signType: 'MD5',
							paySign: paymentData.paySign,
							success: (res) => {
								uni.showToast({
									title: "支付成功"
								})
								uni.navigateTo({
									url: 'submit?seqId='+seqId
								})
							},
							fail: (res) => {
								uni.showModal({
									content: "支付失败",
									showCancel: false
								})
							},
							complete: (res) => {
								console.log(res);
							}
						})
					}else if(res.code==10012){
						uni.navigateTo({
							url:'/pages/login/login_wechat'
						})
					}
				})
				// #endif
			},
		}
	}
</script>

<style lang="scss">
	.box {
		background: white;
		width: 100%;
		height: calc(100vh - var(--window-top) - var(--window-bottom))
	}

	.next-btn {
		width: calc(100% - 60rpx);
		background: $color-primary;
		font-size: 30rpx;
		height: 90rpx;
		line-height: 90rpx;
		text-align: center;
		color: white;
		margin: 40rpx auto;
		border-radius: 10rpx;
	}

	.info-li {
		width: calc(100% - 60rpx);
		padding: 0 30rpx;
		min-height: 100rpx;
		align-items: center;
		display: flex;
		justify-content: space-between;
		font-size: 28rpx;
		border-bottom: 1px solid #eee;

		.left {
			display: flex;
			align-items: center;

			.iconfont {
				font-size: 40rpx;
				margin-right: 10rpx;
				flex-shrink: 0;
			}

			.title {
				width: 150rpx;
				flex-shrink: 0;
				font-size: 30rpx;
			}
		}

		.info-li-input {
			flex-grow: 1;
			text-align: right;
			font-size: 28rpx;
			height: 100rpx;
			line-height: 100rpx;
		}
	}

	.gender-l {
		margin-right: 18rpx;
	}

	.yzm-btn {
		padding: 12rpx 20rpx;
		margin-left: 10rpx;
	}


	.info-photo-li {
		flex-wrap: wrap;

		.card-title {
			width: 100%;
			height: 100rpx;
			line-height: 100rpx;
			font-size: 30rpx;
		}
	}

	.photo_list {
		width: 100%;
		height: auto;
		display: flex;
		margin-bottom: 40rpx;
		justify-content: space-between;

		.photo_li {
			width: 220rpx;
			height: 200rpx;
			background: #F2F2F2;
			border-radius: 7rpx;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;
			position: relative;

			&:last-child {
				margin-right: 0;
			}

			.photo_default {
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				justify-content: center;
				cursor: pointer;
			}

			.default_icon {
				width: 53rpx;
				height: 46rpx;
				text-align: center;
				margin: 0 auto;
			}

			.default_text {
				width: 100%;
				text-align: center;
				font-size: 24rpx;
				margin-top: 16rpx;
				color: #666;
			}

			.img {
				width: 100%;
				height: 100%;
			}

			.close_icon {
				width: 32rpx;
				height: 32rpx;
				position: absolute;
				top: -10rpx;
				right: -10rpx;
				z-index: 2;
				cursor: pointer;
			}
		}

	}
</style>
